@mixin flex {
  display: flex;
  justify-content: center;
  align-items: center;
}
.notesContainer {
  @include flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  position: relative;
  background-color: #ececec;
  animation: intr 0.2s ease forwards;
  @keyframes intr {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  .logo {
    @include flex();
    background-color: white;
    width: 96.5vw;
    height: 50px;
    text-align: center;
    border-radius: 10px;
    margin-top: 10px;
    box-shadow: 6px 6px 5px #bbb, 0 10px 5px rgba(0, 0, 0, 0);
    position: relative;
    overflow: hidden;
    button {
      @include flex;
      flex-direction: column;
      position: absolute;
      right: 0;
      height: 100%;
      width: 50px;
      margin-right: 0px;
      background: #dfdfdf;
      border: none;
      font-size: 20px;
      cursor: pointer;
      .burger {
        @include flex();
        flex-direction: column;
        position: relative;
        height: 100%;
        width: 50px;
        z-index: 11;
        div {
          width: 35px;
          height: 6px;
          border-radius: 25px;
          background: white;
          position: absolute;
        }
        div:nth-child(1) {
          transform: translateY(-12px);
        }
        div:nth-child(3) {
          transform: translateY(12px);
        }
      }
    }
  }
  .offlineAlert {
    @include flex();
    width: 96.5vw;
    height: 100px;
    margin-top: 10px;
    font-size: 25px;
    border-radius: 10px;
    transition: all 0.3s;
    letter-spacing: 2px;
    animation: fadeIN 0.7s forwards;
    @keyframes fadeIN {
      0% {
        opacity: 0;
        height: 10px;
      }
      100% {
        opacity: 1;
        height: 100px;
      }
    }
  }
  .noteCon {
    @include flex;
    justify-content: flex-start;
    flex-direction: column;
    width: 100%;
    min-height: 100vh;
    margin: 10px;
    margin-top: 5px;
    .notes {
      width: 96.5vw;
      margin: 5px;
      border-radius: 10px;
      box-shadow: rgb(187, 187, 187) 0px 0px 10px;
      transition: all 0.3s;
      animation: fadeIN 0.7s forwards;
      @keyframes fadeIN {
        0% {
          opacity: 0;
        }
        100% {
          opacity: 1;
        }
      }
      h1 {
        font-size: 45px;
        width: 100%;
        height: 60px;
        padding: 10px;
        text-align: center;
        font-weight: bold;
        word-wrap: normal;
        pointer-events: none;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      div {
        width: 100%;
        padding: 10px;
        text-align: center;
        word-wrap: break-word;
        pointer-events: none;
        a {
          color: rgb(0, 98, 255);
          user-select: none;
          z-index: 10;
          pointer-events: none;
        }
      }
      .date {
        font-size: 15px;
        width: 100%;
        padding: 0 10px 10px 10px;
        text-align: end;
        color: #000000;
        pointer-events: none;
        opacity: 0.5;
      }
    }
  }
}

.cancelAnime {
  opacity: 1;
  animation: fadeOut 0.3s forwards;
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
.openAnime {
  animation: fadeIn 0.3s forwards;
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.openBurger1 {
  animation: oBur1 0.5s forwards;
}
.openBurger2 {
  animation: oBur2 0.5s forwards;
}
.openBurger3 {
  animation: oBur3 0.5s forwards;
}
.closeBurger1 {
  animation: cBur1 0.5s backwards;
}
.closeBurger2 {
  animation: cBur2 0.5s backwards;
}
.closeBurger3 {
  animation: cBur3 0.5s backwards;
}
@keyframes oBur1 {
  0% {
    transform: translateY(12px) rotate(0deg);
  }
  100% {
    transform: translateY(0) rotate(45deg);
  }
}
@keyframes oBur3 {
  0% {
    transform: translateY(-12px) rotate(0deg);
  }
  100% {
    transform: translateY(0) rotate(-45deg);
  }
}
@keyframes oBur2 {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes cBur1 {
  0% {
    transform: translateY(0) rotate(45deg);
  }
  100% {
    transform: translateY(12px) rotate(0deg);
  }
}
@keyframes cBur3 {
  0% {
    transform: translateY(0) rotate(-45deg);
  }
  100% {
    transform: translateY(-12px) rotate(0deg);
  }
}
@keyframes cBur2 {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.notesFadeOut {
  animation: fadeOutNFO 0.2s forwards;
}
@keyframes fadeOutNFO {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@media only screen and (min-width: 792px) {
  .notesContainer {
    justify-content: flex-start !important;
  }
  .noteCon {
    width: 96.5vw !important;
    align-items: flex-start !important;
    justify-content: flex-start !important;
    flex-direction: unset !important;
    flex-wrap: wrap;
    min-height: unset !important;
    .notes {
      width: unset !important;
      padding: 0px !important;
      margin: 10px !important;
      h1 {
        padding: 17px 10px 0px 10px !important;
        font-size: 30px !important;
        font-weight: 500 !important;
      }
      p {
        font-size: 10px !important;
      }
      .date {
        padding: 0px 0px 0px 10px;
      }
    }
  }
  .logo {
    background-color: red;
    justify-content: flex-start !important;
    padding-left: 15px;
    overflow: visible !important;
    h1 {
      position: absolute;
      z-index: 2;
    }
    button {
      display: none !important;
      .burger {
        display: none;
      }
    }
  }
}
